import { NavController, NavParams, Platform } from 'ionic-angular';
import { Component, OnInit, ViewChild, ViewChildren, QueryList } from '@angular/core';

import { HttpClient } from '@angular/common/http';

import { Renderer2 } from '@angular/core';

import { VideoPaneDirective } from '../../directives/video-pane/video-pane';
import { Diagnostic } from '@ionic-native/diagnostic';

declare var OT: any;

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage implements OnInit {

  @ViewChild('currentVideo') currentVideo: any;

  @ViewChildren(VideoPaneDirective)
  videoPanes: QueryList<any>

  apiKey;
  sessionId;
  token;
  session;
  publisher;
  publishVideo = true;
  publishAudio = true;
  roomName;
  userName;

  isConnected = false;

  subscriber;


  participants = [];
  audioState = true;
  videoState = true;
  subscribers = [];

  activeParticipant = null;
  unregisterBackButtonAction;

  showDefaultPic = false;


  constructor(private http: HttpClient, public navCtrl: NavController, public navParams: NavParams, public renderer: Renderer2, private diagnostic: Diagnostic, private platform: Platform) {

    this.roomName = this.navParams.get("roomName");
    this.userName = this.navParams.get("userName");

  }

  ngOnInit() {

    this.requestRequiredPermissions();
  }

  ionViewDidEnter() {
    this.initializeBackButtonCustomHandler();
  }

  ionViewWillLeave() {
    // Unregister the custom back button action for this page
    this.unregisterBackButtonAction && this.unregisterBackButtonAction();
  }

  public initializeBackButtonCustomHandler(): void {
    this.unregisterBackButtonAction = this.platform.registerBackButtonAction(() => {
      this.handleBack();
    }, 10);
  }

  handleBack() {
    this.endCall();
  }

  // Toggle audio state
  toggleAudio() {

    this.audioState = !this.audioState;
    this.publisher.publishAudio(this.audioState);
    this.publisher.setStyle('videoDisabledDisplayMode', (this.audioState == true ? 'off' : 'on'));
  }

  // Toggle video state
  toggleVideo() {

    this.videoState = !this.videoState;
    this.publisher.publishVideo(this.videoState);
    this.publisher.setStyle('videoDisabledDisplayMode', (this.videoState == true ? 'off' : 'on'));
  }

  endCall() {
    console.log('inside end call');
    if (this.session && this.isConnected) {
      this.session.disconnect();
    }
    this.navCtrl.pop();
  }

  startCall() {

    // (optional) add server code here
    var SERVER_BASE_URL = 'https://videochat-opentok-node.herokuapp.com';
    this.http.get(SERVER_BASE_URL + '/room/' + this.roomName).subscribe((res: any) => {
      console.log(res);
      this.apiKey = res.apiKey;
      this.sessionId = res.sessionId;
      this.token = res.token;
      this.initializeSession();
    }, this.handleError);
  }

  // Handling all of our errors here by alerting them
  handleError(error) {
    if (error) {
      alert("Please check your network connection and try again.");
      this.endCall();
    }
  }


  requestRequiredPermissions() {
    this.diagnostic.requestRuntimePermissions([
      this.diagnostic.permission.CAMERA,
      this.diagnostic.permission.RECORD_AUDIO
    ]).then((statuses) => {
      var isPermissionGranted = true;
      for (var permission in statuses) {
        switch (statuses[permission]) {
          case this.diagnostic.permissionStatus.GRANTED:
            console.log("Permission granted to use " + permission);
            break;
          case this.diagnostic.permissionStatus.NOT_REQUESTED:
            console.log("Permission to use " + permission + " has not been requested yet");
          case this.diagnostic.permissionStatus.DENIED:
            console.log("Permission denied to use " + permission + " - ask again?");
          case this.diagnostic.permissionStatus.DENIED_ALWAYS:
            console.log("Permission permanently denied to use " + permission + " - guess we won't be using it then!");
          default:
            isPermissionGranted = false;
        }
      }

      if (isPermissionGranted) {
        this.startCall();
      } else {
        alert("We need camera and microphone permissions to start the video call");
      }
    }, (error) => {
      if (error == "cordova_not_available") {
        this.startCall();
      } else {
        alert("Please check your network connection and try again.");
        this.endCall();
      }
      console.error("The following error occurred: " + error);
    });
  }



  initializeSession() {
    this.session = OT.initSession(this.apiKey, this.sessionId);

    this.session.on('streamCreated', (event) => {


      this.addParticipant(event.stream);

      var subscriber = this.session.subscribe(event.stream,
        { insertDefaultUI: false, insertMode: 'append', width: '100%', height: '100%' }, this.handleError);


      subscriber.on('videoElementCreated', event => {

        let id = event.target.stream.id;
        let participant = this.getParticipantOfId(id);

        if (participant) {
          console.log("Add video for participant", participant);
          participant.video = event.element;
        }

        if (!this.activeParticipant) {
          this.showParticipantInMainView(participant);
        }

        console.log("Current participants ", this.participants);
      });

      subscriber.on({
        videoDisabled: (event) => {
          console.log("inside video disabled");
          subscriber.setStyle('videoDisabledDisplayMode', 'on');

          let _id = event.target.stream.id;

          if (_id == this.activeParticipant.id) { // show pic
            this.showDefaultPic = true;
          }
        },
        videoEnabled: (event) => {
          console.log("inside video enabled");
          subscriber.setStyle('videoDisabledDisplayMode', 'off');
          let _id = event.target.stream.id;

          if (_id == this.activeParticipant.id) { // hide pic
            this.showDefaultPic = false;
          }
        }
      });
    });

    this.session.on("streamDestroyed", (event) => {

      console.log("Stream " + event.stream.name + " ended. " + event.reason);
      let _id = event.stream.id;
      let _participant = this.getParticipantOfId(_id);
      let deleteIndex = this.participants.indexOf(_participant);

      this.participants.splice(deleteIndex, 1);

      if (this.participants.length > 0) {

        let p = this.participants[0];
        this.showParticipantInMainView(p);
      }
    })

    let pub_name = this.userName;

    // Create a publisher
    this.publisher = OT.initPublisher('publisher', {
      insertMode: 'append',
      width: '100%',
      height: '100%',
      name: pub_name,
      style: { nameDisplayMode: "off", buttonDisplayMode: "off" }
    }, this.handleError);

    this.publisher.on('videoElementCreated', event => {

      console.log("Video published.")
    });

    // Connect to the session
    this.session.connect(this.token, (error) => {
      // If the connection is successful, publish to the session
      if (error) {
        this.handleError(error);
      } else {
        this.isConnected = true;
        this.session.publish(this.publisher, this.handleError);
      }
    });
  }

  // Add the participant details from the stream object
  addParticipant(stream) {

    let participant = {
      id: stream.id,
      name: stream.name
    }

    console.log("P added ", participant);

    this.participants.push(participant);
  }

  // Return the participant for given id
  getParticipantOfId(id) {

    let participant = this.participants.find((p) => {
      return p.id == id;
    });

    return participant;
  }

  // Show the given participant in main view
  showParticipantInMainView(participant) {

    if (participant != this.activeParticipant) {
      this.activeParticipant = participant;

      this.showDefaultPic = false;
      
      document.getElementById("mainView").innerHTML = "";
      document.getElementById("mainView").appendChild(participant.video);
    }
  }

}




